<template>
 <div class="flex xl:flex-row flex-col justify-between mb-8">
    <div class="lg:flex lg:divide-x lg:divide-grey-01">
        <div class="flex items-center lg:pr-4 lg:mb-0 mb-4">
          <router-link :to="{ name: 'settingFaq' }" class="text-dark mr-4"><span class="material-icons">chevron_left</span></router-link>
          <h5>Add FAQ</h5>
        </div>
        <div class="lg:pl-4 flex items-center">
          <Breadcrumb :items="breadcrumb">
            <span class="material-icons">chevron_right</span>
          </Breadcrumb>
        </div>
      </div>
  </div>
  <Card>
    <FaqForm ref="refForm" v-model:data="formData" :categories="faqCategories" />
    <template #footer>
      <div class="flex justify-end">
        <Button color="teal" view="smooth" @click="createFaq" :loading="loading.create">Submit</Button>
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useFaq } from 'composable/setting'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
import FaqForm from 'components/Faq/Form.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Card,
    Input,
    Button,
    Breadcrumb,
    Pagination,
    FaqForm
  },
  async setup(props, { emit }) {
    const { formData, loading, actionAll, actionCreate, stateAll } = useFaq()
    const refForm = ref(null)
    const router = useRouter()
    const breadcrumb = [{
      text: 'Settings',
      to: {
        name: 'setting'
      }
    }, {
      text: 'FAQ',
      to: {
        name: 'settingFaq'
      }
    },{
      text: 'Add FAQ'
    }]
    const faqCategories = ref([])
    const createFaq = async () => {
      loading.create = true
      const isValid = refForm.value?.validate && refForm.value.validate() || false
      if(isValid) await actionCreate(() => {
        router.push({
          name: 'settingFaq'
        })
        ElMessage({
          type: 'success',
          message: 'FAQ created'
        })
      })
    }
    await actionAll()
    faqCategories.value = stateAll.value.faq_categories || []

    return {
      refForm,
      formData,
      loading,
      breadcrumb,
      faqCategories,
      createFaq
    }
  }
})
</script>